import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';
import { NgZorroAntModule } from '../ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { LottieModule } from 'ngx-lottie';
import { playerFactory } from 'src/app/app.module';
import { AccountInformationComponent } from './components/account-information/account-information.component';
const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children: [
      {
        path: 'overviews',
        loadChildren: () => import('../menu/overviews/overviews.module').then(m => m.OverviewsModule)
      },
      // {
      //   path: 'order',
      //   loadChildren: () => import('../menu/order/order.module').then(m => m.OrderModule)
      // },
      {
        path: 'management',
        loadChildren: () => import('../menu/management/management.module').then(m => m.ManagementModule)
      },
      {
        path: 'printer',
        loadChildren: () => import('../menu/management/printer/printer.module').then(m => m.PrinterModule)
      },
      {
        path: 'settings',
        loadChildren: () => import('../menu/settings/settings.module').then(m => m.SettingsModule)
      }, 
      {
        path: 'developers',
        loadChildren: () => import('../menu/developers/developers.module').then(m => m.DevelopersModule)
      },
    ]
  }
]

@NgModule({
  declarations: [
    DashboardComponent,AccountInformationComponent
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    LottieModule.forRoot({ player: playerFactory }),
  ]
})
export class DashboardModule { }
